import { createSlice, createSelector } from '@reduxjs/toolkit';
const initialState = {
    arr: [],
    map: {},
    name: null,
};
const entryFileSlice = createSlice({
    name: 'entryFile',
    initialState,
    reducers: {
        loadFile: (state, action) => {
            state.name = action.payload.filename;
            state.arr = action.payload.data;
            state.map = {};
            for (const item of state.arr) {
                state.map[item.id] = item;
            }
        },
        sortBy: (state, action) => {
            const { type, field } = action.payload;
            state.arr = state.arr.sort((a, b) => {
                if (typeof a !== typeof b ||
                    a[field] === b[field])
                    return 0;
                if (typeof a === 'boolean') {
                    if (type === 'ASC')
                        return Number(a[field]) - Number(b[field]);
                    return Number(a[field]) - Number(b[field]);
                }
                if (type === 'ASC')
                    return a[field] > b[field] ? 1 : -1;
                return b[field] > a[field] ? 1 : -1;
            });
        },
        updateField: (state, action) => {
            const { id, name, value } = action.payload;
            const itemIndex = state.arr.findIndex(el => el.id === id);
            const item = state.arr[itemIndex];
            if (!item)
                return;
            const newItem = Object.assign(Object.assign({}, item), { [name]: value });
            state.arr[itemIndex] = newItem;
            state.map[id] = newItem;
        },
    },
});
export const { loadFile, sortBy, updateField } = entryFileSlice.actions;
export default entryFileSlice.reducer;
export const itemFieldSelector = createSelector((state) => state.entryFile.map, (_state, _name, id) => id, (_state, name, _id) => name, (map, id, name) => {
    const item = map[id];
    if (!item)
        return undefined;
    return item[name];
});
